        </div>
        <div class="bg-bottom">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="#ffffff" fill-opacity="1" d="M0,160L80,154.7C160,149,320,139,480,154.7C640,171,800,213,960,218.7C1120,224,1280,192,1360,176L1440,160L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
          </svg>
        </div>
        <div class="bg-bottom bg-bottom--shadow">
          <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 1440 320">
            <path fill="#ffffff" fill-opacity="1" d="M0,160L80,154.7C160,149,320,139,480,154.7C640,171,800,213,960,218.7C1120,224,1280,192,1360,176L1440,160L1440,320L1360,320C1280,320,1120,320,960,320C800,320,640,320,480,320C320,320,160,320,80,320L0,320Z"></path>
          </svg>
        </div>
      </header>     
      <h1>{{ msg }}</h1>
      <form class="info-user form-cargo">
        <div class="info-user__title title">Personal Info</div>
        <PersonalInfo
          v-for="infouser in INFOUSER"
          :key="infouser.id"
          :user_data="infouser"
        />
        <hr />
        <div class="items-list">
          <div class="title">Items List</div>
          <div class="items-list--row" v-for="(name, i) in ITEMLIST" :key="i" :data-index="name.id">
            <div class="row">
              <input
                class="form-control"
                v-model="ITEMLIST[i].name"
                @input="filterDate(name.name)"
                type="text"
                autocomplete="off"
                @focus="ITEMLIST[i].modal = true"
                :data-id="'name-' + name.id"
                ref="listOfItems"
              />
              <span class="material-icons cur-pointer color-danger p-2" @click="deleteItem(i)" v-show="ITEMLIST.length > 1">delete_forever</span>
            </div>
            <input type="checkbox" :id="name.id" v-model="ITEMLIST[i].disassemble"><label :for="name.id">disassemble</label>
            <!-- @blur="ITEMLIST[i].modal = false"             -->
            <div class="list-arr" v-if="filterItems && ITEMLIST[i].modal" :data-id="i">
              <Items
                v-for="infoproducts in filteredDate"
                :key="infoproducts.id"
                :item_data="infoproducts"
                @setItem="setItem(infoproducts.name, i)"
              />
            </div>
            <div
              @click="addNewRow()"
              v-show="i == ITEMLIST.length - 1"
              class="add-more p-2"
            >
              add more <span class="material-icons">add_circle</span>
            </div>
          </div>
        </div>
        <button class="btn">Send</button>
      </form>
    </div>
  </div>
</template>

<script>
import PersonalInfo from "@/components/PersonalInfo.vue";
import Items from "@/components/Items.vue";
import { mapGetters, mapActions } from "vuex";

export default {
  name: "Info",
  components: {
    PersonalInfo,
    Items,
  },
  props: {
    msg: String,
  },
  data() {
    return {
      state: [],
      filteredDate: [],
    };
  },
  computed: {
    ...mapGetters(["INFOUSER", "INFOPRODUCTS", "FILTER_VALUE", "ITEMLIST"]),
  },
  methods: {
    ...mapActions(["GET_INFO_FROM_API", "GET_FILTER_VALUE", "GET_LIST_ITEM", "DELETE_ITEM"]),
    filterDate(value) {
      this.GET_FILTER_VALUE(value);
    },
     listOfItems(value) {
       this.GET_LIST_ITEM(value);
    },
    filterItems(value) {
      let vm = this;
      vm.filteredDate = [...this.INFOPRODUCTS];
      if (value) {
        vm.filteredDate = vm.filteredDate.filter((item) => {
          return item.name.toLowerCase().includes(value.toLowerCase());
        });
      } else {
        vm.filteredDate = vm.INFOPRODUCTS;
      }
    },
    setItem(value,index) {
      let vm = this;
      
      vm.state = [...this.ITEMLIST];
      
      vm.state.forEach((item) => {
         item.modal = false;
      });
      vm.state[index].name = value
      vm.filteredDate = [];
    },
    addNewRow(name) {
      let vm = this;
      let id = vm.ITEMLIST.length
      vm.ITEMLIST.push({
        id: id,
        name: name,
        qty: "",
        disassemble: false,
        modal: false
      });
      this.filteredDate = [];
    },
    deleteItem(index){
      this.DELETE_ITEM(index);
    },
  },
  watch: {
    FILTER_VALUE() {
      this.filterItems(this.FILTER_VALUE);
    },
    ITEMLIST(){
      this.setItem(this.ITEMLIST)
    },
  },
  mounted() {
    this.GET_INFO_FROM_API().then((response) => {
      this.filterItems(this.FILTER_VALUE);
      if (response.data) {
        console.log("data arrived");
      }
    });
  },
};
</script>

